import React, { useState, useRef, Fragment } from "react";
import {
  FormHelperText,
  TextField,
  Button,
  FormControlLabel,
  withStyles,
  Snackbar
} from "@material-ui/core";
import Dropzone from 'react-dropzone';
import { Form, Row, Col} from 'react-bootstrap';
import MuiAlert from '@material-ui/lab/Alert'
import FormDialog from "../../../shared/components/FormDialog";
import HighlightedInformation from "../../../shared/components/HighlightedInformation";
import ButtonCircularProgress from "../../../shared/components/ButtonCircularProgress";
import VisibilityPasswordTextField from "../../../shared/components/VisibilityPasswordTextField";
import axios from 'axios';
import { Router } from "@material-ui/icons";
import { string } from "prop-types";

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
  }

const styles = (theme) => ({
    link: {
      transition: theme.transitions.create(["background-color"], {
        duration: theme.transitions.duration.complex,
        easing: theme.transitions.easing.easeInOut,
      }),
      cursor: "pointer",
      color: theme.palette.primary.main,
      "&:enabled:hover": {
        color: theme.palette.primary.dark,
      },
      "&:enabled:focus": {
        color: theme.palette.primary.dark,
      },
    },
  });

function AdminSetting(props) {
  var temp = "ab";
    const [status, setStatus] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    const firstName = useRef();
    const lastName = useRef();
    const [isPasswordVisible, setIsPasswordVisible] = useState(false);
    const registerPassword = useRef();
    const registerPasswordRepeat = useRef();
    const email = useRef();
    const dropRef = useRef();
    const [previewSrc, setPreviewSrc] = useState('');
    const [file, setFile] = useState(null);
    const [isPreviewAvailable, setIsPreviewAvailable] = useState(false);
    const [formact, setformact] = useState(true);
    const [notiopen, setnotiopen] = useState(false);
    const [defFirstname,setdefFirstname] = useState();
    const [defLastname,setdefLastname] = useState();
    const [defEmail,setdefEmail] = useState();
    const [defPassword,setdefPassword] = useState();
    const [defRepeatpassword,setdefRepeatpassword] = useState();
    const [defImage,setdefImage] = useState();
    const [waiting, setWaiting] = useState(true);

    if (waiting) {
      axios({
          method: "POST",
          url: "http://3.139.50.101:4000/adminControl/getadmin"
      }).then((response)=>{
          setdefFirstname(response.data.firstname);
          setdefLastname(response.data.lastname);
          setdefEmail(response.data.email);
          setdefPassword(response.data.password);
          setdefRepeatpassword(response.data.password);
          setWaiting(false);
          console.log(defFirstname);
      });
    }
    // 

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
          return;
        }
    
        setnotiopen(false);
      };

    const onDrop = (files) => {
        const [uploadedFile] = files;
        setFile(uploadedFile);
    
        const fileReader = new FileReader();
        fileReader.onload = () => {
          setPreviewSrc(fileReader.result);
        };
        fileReader.readAsDataURL(uploadedFile);
        setIsPreviewAvailable(uploadedFile.name.match(/\.(jpeg|jpg|png)$/));
        dropRef.current.style.border = '2px dashed #e9ebeb';
      };

      const updateBorder = (dragState) => {
        if (dragState === 'over') {
          dropRef.current.style.border = '2px solid #000';
        } else if (dragState === 'leave') {
          dropRef.current.style.border = '2px dashed #e9ebeb';
        }
      };

      const Editprofile = () =>{
         setformact(false);
         console.log(defFirstname);
         return;

      }
      const saveprofile = async (e)=>{
        e.preventDefault();
        if(registerPassword.current.value.length > 6){
            if(registerPassword.current.value === registerPasswordRepeat.current.value){
                if(file){
                    var xhr = new XMLHttpRequest();
                    const requestData = new FormData();
                    requestData.append("avatar", file);
                    requestData.append("email", email.current.value)
                    requestData.append("firstname", firstName.current.value)
                    requestData.append("lastname", lastName.current.value)
                    requestData.append("password", registerPassword.current.value)
                    requestData.append("role", "admin")

                    xhr.addEventListener("readystatechange", function() {
                        if(this.readyState === 4) {
                          if(this.response.status === "error"){
                          } else {
                            console.log(this.response) 
                          }
                        } 
                    });
                    xhr.upload.addEventListener("progress", progressFunction, false);  
                    xhr.open("POST", "http://3.139.50.101:4000/adminControl/adminSetting");
                    xhr.send(requestData);
                    

                    function progressFunction(evt){  
                        if (evt.lengthComputable) {  
                            console.log(Math.round(evt.loaded / evt.total * 100))
                          if(Math.round(evt.loaded / evt.total * 100) === 100){
                            setformact(true);
                          }
                        }  
                    }  

                } else {
                    setnotiopen(true);
                }
            } else {
                setStatus("passwordsDontMatch");
                return;
            }
        } else {
            setStatus("passwordTooShort");
            return;
        }
        setStatus(null);
        setIsLoading(true);
        setTimeout(() => {
        setIsLoading(false);
        }, 1500);
      }
    return (
      <div>
      {
        waiting === true ? null :
        <Fragment>
            <Snackbar open={notiopen} autoHideDuration={6000} onClose={handleClose}>
              <Alert onClose={handleClose} severity="error">
                Please add avatar in profile!
              </Alert>
            </Snackbar>
        <Button variant="contained" size = "large" color = "secondary" onClick = {Editprofile}>Edit profile</Button>
        <Form>
        <TextField
            required
            defaultValue = {defFirstname}
            disabled = {formact}
            variant="filled"
            margin="normal"
            fullWidth
            error={status === "Please input your firstname"}
            label="First Name"
            inputRef={firstName}
            autoFocus
            autoComplete="off"
            type="text"
            onChange={() => {
              if (status === "Please input your firstname") {
                setStatus(null);
              }
            }}
            FormHelperTextProps={{ error: true }}
          />
          <TextField
          defaultValue = {defLastname}
            disabled = {formact}
            variant="filled"
            margin="normal"
            required
            fullWidth
            error={status === "Please input your lastname"}
            label="Last Name"
            inputRef = {lastName}
            autoComplete="off"
            type="text"
            onChange={() => {
              if (status === "Please input your lastname") {
                setStatus(null);
              }
            }}
            FormHelperTextProps={{ error: true }}
          />
           <TextField
           defaultValue = {defEmail}
           disabled = {formact}
            variant="filled"
            margin="normal"
            required
            fullWidth
            error={status === "invalidEmail" || status === "emailExist"}
            label="Email Address"
            inputRef = {email}
            autoComplete="off"
            type="email"
            onChange={() => {
              if (status === "invalidEmail") {
                setStatus(null);
              }
            }}
            helperText={(() => {
              if(status === "emailexist")
              {
                return "email already exist"
              }
              return null;
            })()}
            FormHelperTextProps={{ error: true }}
          />
          <VisibilityPasswordTextField
          defaultValue = {defPassword}
            disabled = {formact}
            variant="filled"
            margin="normal"
            required
            fullWidth
            error={
              status === "passwordTooShort" || status === "passwordsDontMatch"
            }
            label="Password"
            inputRef={registerPassword}
            autoComplete="off"
            onChange={() => {
              if (
                status === "passwordTooShort" ||
                status === "passwordsDontMatch"
              ) {
                setStatus(null);
              }
            }}
            helperText={(() => {
              if (status === "passwordTooShort") {
                return "Create a password at least 6 characters long.";
              }
              if (status === "passwordsDontMatch") {
                return "Your passwords dont match.";
              }
              return null;
            })()}
            FormHelperTextProps={{ error: true }}
            isVisible={isPasswordVisible}
            onVisibilityChange={setIsPasswordVisible}
          />
          <VisibilityPasswordTextField
          defaultValue = {defPassword}
            disabled = {formact} 
            variant="filled"
            margin="normal"
            required
            fullWidth
            error={
              status === "passwordTooShort" || status === "passwordsDontMatch"
            }
            label="Repeat Password"
            inputRef={registerPasswordRepeat}
            autoComplete="off"
            onChange={() => {
              if (
                status === "passwordTooShort" ||
                status === "passwordsDontMatch"
              ) {
                setStatus(null);
              }
            }}
            helperText={(() => {
              if (status === "passwordTooShort") {
                return "Create a password at least 6 characters long.";
              }
              if (status === "passwordsDontMatch") {
                return "Your passwords dont match.";
              }
            })()}
            FormHelperTextProps={{ error: true }}
            isVisible={isPasswordVisible}
            onVisibilityChange={setIsPasswordVisible}
          />
          <div className="upload-section">
          <Dropzone
            disabled = {formact}
            onDrop={onDrop}
            onDragEnter={() => updateBorder('over')}
            onDragLeave={() => updateBorder('leave')}
          >
            {({ getRootProps, getInputProps }) => (
              <div {...getRootProps({ className: 'drop-zone' })} ref={dropRef}>
                <input {...getInputProps()} />
                <p style = {{borderStyle:"inset", cursor:"pointer"}}>Drag and drop a file OR click here to select a file</p>
                {file && (
                  <div>
                    <strong>Selected file:</strong> {file.name}
                  </div>
                )}
              </div>
            )}
          </Dropzone>
          {previewSrc ? (
            isPreviewAvailable ? (
              <div className="image-preview">
                <img className="preview-image" src={previewSrc} alt="Preview" style = {{width:"100%"}} />
              </div>
            ) : (
              <div className="preview-message">
                <p>No preview available for this file</p>
              </div>
            )
          ) : (
            <div className="preview-message">
              <p>Image preview will be shown here after selection</p>
            </div>
          )}
        </div>
        {!formact?
        <Button variant="contained" fullWidth size = "large" color = "primary" type="submit" disabled={isLoading} onClick = {saveprofile}>
          Save
        </Button>
        :null
        }
        </Form>
      </Fragment>
      }
      </div>
    );
  }
export default AdminSetting;